
.DefaultLayout {
    min-height: 100vh;
    background: black;
    color: white;
}

.page-content {
    padding: 0 25px 0 25px;
    justify-self: center;
    text-align: left;
    flex-grow: 1;
}


.site-header {
    padding-top: 10px;
    background-color: #00B0F0;
    color: black;
}

.site-footer {
    padding-top: 1vh;
    padding-bottom: 1vh;
    text-align: center;
}

.cahaba-logo {
    margin-left: 25px;
    font-family: "Agency FB Bold";
    flex-grow: 1;
    text-decoration: none;
}
.cahaba-logo-main {
    padding: 0 0 0 0;
    margin: 0 0 0 0;
    color: white;
    font-size: 15vh; /* Logo arguably should scale with screen size */
    text-decoration: none;
}
.cahaba-logo-sub {
    padding: 0 0 0 0;
    margin: -15px 0 0 3px;
    color: black;
    font-size: 5vh; /* Logo arguably should scale with screen size */
}

.cahaba-logo a {
    text-decoration: inherit;
}



.menu-container {
    padding: 0px;
}

.menu-box {
    font-family: "Agency FB Bold", sans-serif;
    font-size: 32px;
    display: block;
    padding: 0px;
    margin-left: 10px;
    margin-right: 10px;
}



td, th {
    border: 1px solid white;
}

